body {
    margin: 0;
    padding: 0;
    font-family: 'VT323', monospace;
    color: #00ee00;
    background: #111111;
    /*font-size: 1.5em;*/
    font-size: 2.5em;
    line-height: 1.3em;
}

a {
    color: #3366CC;
}

input,
select,
textarea,
button {
    font-family: inherit;
    font-size: inherit;
    color: inherit;
}

.game-intro {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100vh;
}

.game-intro-subtitle {
    font-weight: normal;
    /*font-size: 1.5em;*/
    font-size: 2.5em;
    text-align: center;
}

.game-intro-enter {
    font-weight: normal;
    /*font-size: 1em;*/
    font-size: 1.5em;
    text-align: center;
}

.image.intro {
    line-height: 2vw;
}

.intro.imageLine {
    font-size: 2vw;
}

.events-container {
    margin-bottom: 3em;
    padding: 1em;
}

.command-block {
    padding-left: 1em;
}

#input {
    width: 100%;
    position: fixed;
    background: #111111;
    bottom: 0;
    padding: 1em;

    display: -ms-flexbox;

    display: flex; /* or inline-flex */
    -ms-flex-direction: row;
        flex-direction: row;
}

#input-tag {
    white-space: pre;
}

#input-element {
    background: transparent;
    border: none;
    outline: none;
    -ms-flex: 1 1;
        flex: 1 1;
}

.image {
    margin: 0 auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    line-height: 1.5vw;
}

.imageLine {
    font-size: 1.5vw;
    white-space: pre;
    overflow-x: hidden;
}

.command-block-key,
.command-block-tip {
    display: inline-block;
    white-space: pre-wrap;
}

.sos-events p {
    margin: 0;
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0,0,0,0);
    border: 0;
}

.title {
    font-weight: normal;
    font-size: 1em;
}

@media (max-width: 481px) {
    /* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */
    body {
        font-size: 1em;
    }

    .image {
        line-height: 2.32vw;
    }

    .imageLine {
        font-size: 2.32vw;
    }
}



